* {
    font: 16px "Noto Sans JP", sans-serif;
    box-sizing: border-box;
    margin: 0;
  }
  
  /* General styles */
  
  .larger-font {
    font-size: 22px;
  }
  
  .largest-font {
    font-size: 35px;
  }
  
  .bold {
    font-weight: 600;
  }
  
  .hover-hl:hover {
    background-color: yellow;
  }
  
  button, a {
    cursor: pointer;
  }
  
  
  /* Main page styling*/
  
  main {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin: 0px 80px 50px 80px;
    gap: 70px;
  }
  
  /* For smaller screens */
  
  @media only screen and (max-width: 820px) {
    .header-group {
        flex-direction: column;
        align-items: center;
        margin-left: 0px;
    }
  
    .header-group img {
        width: 80%;
    }
  
    header {
        align-items: center;
    }
  
    nav {
        margin: 0px 0px 20px 0px; 
    }
  
    .largest-font {
        font-size: 25px;
    }
  
    .item {
        width: 100%;
    } 
  }